
.search {
  height: 45px;
}
.mint-search {
  height: auto;
}
.container {
  margin-top: 52px;
}
.swiper {
  /*height: 200px;*/
}
.swiper img {
  width: 100%;
  /*min-height: 200px;*/
}
.mint-searchbar {
  /*margin-top: 2px;*/
  padding: 0;
}
.mint-swipe-items-wrap>div {
  width: auto;
  height: auto;
}
.mint-swipe-item {
  /*height: 200px;*/
}
.contentList {
  padding-bottom: 59px;
  background: #fff;
}
.items {
  padding: 0 3%;
}
.items:after {
  content: '.';
  height: 0;
  clear: both;
  visibility: hidden;
}
.title {
  height: 42px;
  text-align: center;
  background-color: #fff;
}
.title-line {
  height: 20px;
  width: 172px;
  margin: 0 auto;
  vertical-align: baseline;
  display: inline-block;
  font-size: 18px;
  line-height: 40px;
  border-bottom: 1px solid #333;
}
.title-fonts {
  height: 40px;
  padding: 0 10px;
  background-color: #fff;
  display: inline-block;
}
.item {
  height: 3rem;
  display: block;
  width: 100%;
  padding: 1rem 0;
  border-bottom: 1px solid #ddd;
  text-decoration: none;
  color: #333;
}
.item-title {
  font-size: 1rem;
  font-weight: 100;
  margin-bottom: 0.5rem;
  text-overflow: -o-ellipsis-lastline;
  overflow: hidden;
  text-overflow: ellipsis;
  display: -webkit-box;
  -webkit-line-clamp: 1;
  -webkit-box-orient: vertical;
}
.item-time {
  color: #999;
  font-size: 0.8rem;
  line-height: 1.6rem;
  font-weight: 100;
}
.swiper-container {
  width: 100%;
  height: 100%;
}
.swiper-slide {
  text-align: center;
  font-size: 18px;
  background: #fff;
  /* Center slide text vertically */
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
  -ms-flex-pack: center;
  justify-content: center;
  -webkit-box-align: center;
  -ms-flex-align: center;
  align-items: center;
}
.swiper-slide img {
  width: 100%;
}
.searchDown,
.searchUp {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
}
.searchDown {
  z-index: 100;
}
.searchUp {
  z-index: 101;
  height: 52px;
  background-color: #58b7ff;
}




